import React from 'react';
import CmtVertical from '../../../../../@coremat/CmtNavigation/Vertical';
import PerfectScrollbar from 'react-perfect-scrollbar';
import makeStyles from '@material-ui/core/styles/makeStyles';
import IntlMessages from '../../../../utils/IntlMessages';

import {
  AccountCircle,
  ArrowForward,
  Category,
  Chat,
  CheckCircle,
  CloudUpload,
  Colorize,
  ContactMail,
  Contacts,
  Domain,
  DonutSmall,
  DragIndicator,
  Edit,
  EditAttributes,
  Email,
  Error,
  Group,
  ImportContacts,
  InsertChart,
  LibraryBooks,
  LocalGroceryStore,
  LockOutlined,
  Map,
  MonetizationOn,
  NotificationImportant,
  Notifications,
  ShowChart,
  Widgets,
} from '@material-ui/icons';
import SidebarButtons from './SIdebarButtons';

const useStyles = makeStyles(theme => ({
  perfectScrollbarSidebar: {
    height: '100%',
    transition: 'all 0.3s ease',
    '.Cmt-sidebar-fixed &, .Cmt-Drawer-container &': {
      height: 'calc(100% - 167px)',
    },
    '.Cmt-modernLayout &': {
      height: 'calc(100% - 72px)',
    },
    '.Cmt-miniLayout &': {
      height: 'calc(100% - 91px)',
    },
    '.Cmt-miniLayout .Cmt-sidebar-content:hover &': {
      height: 'calc(100% - 167px)',
    },
  },
}));

const SideBar = () => {
  const classes = useStyles();
  const navigationMenus = [
    {
      name: <IntlMessages id={'sidebar.dashboards'} />,
      type: 'section',
      children: [
        {
          name: <IntlMessages id={'sidebar.fundoverview'} />,
          icon: <LibraryBooks />,
          type: 'item',
          link: '/dashboard/crypto',
        },
        {
          name: <IntlMessages id={'sidebar.fundmanager'} />,
          icon: <Group />,
          type: 'item',
          link: '/dashboard/managers',
        },
        {
          name: <IntlMessages id={'sidebar.createyourownpool'} />,
          icon: <Group />,
          type: 'item',
          link: '/dashboard/selfmanager',
        },
        {
          name: <IntlMessages id={'sidebar.nyanrewards'} />,
          icon: <MonetizationOn />,
          type: 'item',
          link: '/dashboard/rewards',
        },
        
        // {
        //   name: <IntlMessages id={'sidebar.dashboard.intranet'} />,
        //   icon: <DonutSmall />,
        //   type: 'item',
        //   link: '/dashboard/intranet',
        // },
        // {
        //   name: <IntlMessages id={'sidebar.dashboard.ecommerce'} />,
        //   icon: <LocalGroceryStore />,
        //   type: 'item',
        //   link: '/dashboard/eCommerce',
        // },
        // {
        //   name: <IntlMessages id={'sidebar.dashboard.news'} />,
        //   icon: <ImportContacts />,
        //   type: 'item',
        //   link: '/dashboard/news',
        // },
        // {
        //   name: <IntlMessages id={'sidebar.dashboard.misc'} />,
        //   icon: <LibraryBooks />,
        //   type: 'item',
        //   link: '/dashboard/misc',
        // },
      ],
    },
    {
      name: <IntlMessages id={'sidebar.contractInterfaces'} />,
      type: 'section',
      children: [
        {
          name: <IntlMessages id={'Uniswap'} />,
          type: 'item',
          icon: <ArrowForward />,
          link: '/dapps/uniswap',
        },
        {
          name: <IntlMessages id={'dYdX'} />,
          type: 'item',
          icon: <ArrowForward />,
          link: '/apps/dydx',
        },
        {
          name: <IntlMessages id={'dMex'} />,
          type: 'item',
          icon: <ArrowForward />,
          link: '/apps/dmex',
        }
      ],
    }
  ];

  return (
    <PerfectScrollbar className={classes.perfectScrollbarSidebar}>
      <CmtVertical menuItems={navigationMenus} />
      <SidebarButtons />
    </PerfectScrollbar>
  );
};

export default SideBar;
