import React, { lazy, Suspense } from 'react';
import { Redirect, Route, Switch } from 'react-router';
import PageLoader from '../../@jumbo/components/PageComponents/PageLoader';

const Dashboards = ({ match }) => {
    const requestedUrl = match.url.replace(/\/$/, '');
    return (
      <Suspense fallback={<PageLoader />}>
        <Switch>
          <Redirect exact from={`${requestedUrl}/`} to={`${requestedUrl}/Crypto`} />
          <Route path={`${requestedUrl}/crypto`} component={lazy(() => import('./Crypto'))} />
          <Route path={`${requestedUrl}/managers`} component={lazy(() => import('./Managers'))} />
          <Route path={`${requestedUrl}/rewards`} component={lazy(() => import('./Rewards'))} />
          <Route path={`${requestedUrl}/selfmanager`} component={lazy(() => import('./SelfManager'))} />
          
        </Switch>
      </Suspense>
    );
  };
  
  export default Dashboards;